@media (max-width: 768px) {
    .client-detail, .order-detail{
        max-width: 600px;
    }
    .orders__list{
        grid-template-columns: repeat(auto-fill, minmax(150px, auto));
    }
}

  @media (max-width: 480px) {
    .login__form{
        padding: 25px 0px;
    }
    .header{
        flex-direction: column;
        height: 110px;
    }
    .clients{
        &__list{
            width: 100%;
        }
        &__span__wrapper, &__wrapper{
            flex-direction: column;
        }
        &__search{
            width: auto;
        }
        &__add-btn{
            margin-left: 0;
            margin-top: 10px;
        }
    }
    .client-detail__header, .order-detail__header{
        h2{
            margin: 0 10px;
        }
    }
    .orders{
        &__header{
            flex-direction: column;
        }
        &__search{
            width: auto;
            max-width: none;
        }
        &__add-btn{
            margin-top: 10px;
        }
        &__list{
            grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        }
        &__stats-label{
            margin: 0 5px;
        }
    }
}
